.welcome-appear {
  opacity: 0.01;
  -webkit-transform: translateX(-200px);
      -ms-transform: translateX(-200px);
          transform: translateX(-200px)
}

.welcome-appear.welcome-appear-active {
  opacity: 1;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: all .5s ease-in;
  -o-transition: all .5s ease-in;
  transition: all .5s ease-in;
}

.directions-appear {
  opacity: 0.01;
  -webkit-transform: translateX(-200px);
      -ms-transform: translateX(-200px);
          transform: translateX(-200px)
}

.directions-appear.directions-appear-active {
  opacity: 1;
  -webkit-transform: translateX(0px);
      -ms-transform: translateX(0px);
          transform: translateX(0px);
  -webkit-transition: all 1s ease-in;
  -o-transition: all 1s ease-in;
  transition: all 1s ease-in;
}

.roller-wrapper {
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  margin-top: 10%;
  -webkit-animation: infinite-spinning 2s ease-in-out infinite 4s;
          animation: infinite-spinning 2s ease-in-out infinite 4s;
}

#roller {
  font-size: 7em;
  -webkit-animation-name: slide, infinite-spinning;
          animation-name: slide, infinite-spinning;
  -webkit-animation-duration:  4s;
          animation-duration:  4s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}

@-webkit-keyframes infinite-spinning {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes infinite-spinning {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes slide {
  0% {
    margin-left: 400px;
  }
  100% {
    margin-left: 0px;
  }
}

@keyframes slide {
  0% {
    margin-left: 400px;
  }
  100% {
    margin-left: 0px;
  }
}




@media only screen  and (min-width : 900px) {

  #roller {
    font-size: 10rem;
    margin-top: -5%;
    -webkit-animation-name: slide, infinite-spinning;
            animation-name: slide, infinite-spinning;
    -webkit-animation-duration:  4s;
            animation-duration:  4s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
  }
  
  @-webkit-keyframes infinite-spinning {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      /* transform: translateX(-200px); */
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
      /* transform: translateX(200px); */
    }
  }
  
  @keyframes infinite-spinning {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      /* transform: translateX(-200px); */
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
      /* transform: translateX(200px); */
    }
  }
  
  @-webkit-keyframes slide {
    0% {
      /* transform: rotate(0deg); */
      margin-left: 1500px;
    }
    100% {
      /* transform: rotate(360deg); */
      margin-left: 0px;
    }
  }
  
  @keyframes slide {
    0% {
      /* transform: rotate(0deg); */
      margin-left: 1500px;
    }
    100% {
      /* transform: rotate(360deg); */
      margin-left: 0px;
    }
  }



}


